import React from 'react';

const CompleteCheck = ({ state, theme }) => {
  return (
    <div className={'completeCheck'}>
      {state
        ? <img src={'/assets/images/complete-check.png'} alt={''}/>
        : <img src={`/assets/images/not-complete-check${theme === 'dark' ? '2' : ''}.png`} alt={''}/>
      }
    </div>
  );
};

export default CompleteCheck;
